import React,{useContext} from 'react';
import $ from 'jquery';
import './styles/video.css'
import Chatinv from './Chatinv';
import videoContext from '../context/video-context'

const Video = ({vid,stream})=>{
    console.log('video stream , Video File :',stream,window.fileOwner)
    var video_url = vid
    if(vid == 'copy'){
        video_url = ''
        setTimeout(()=>{
            $('video')[0].srcObject = stream
        },400)
    }
    const defposter = 'https://raw.githubusercontent.com/Ahmetaksungur/twitter-video-player-clone/master/92456705_506989316849451_7379405183454806542_n.jpg';
    
    const orgPoster = defposter;
    const poster = defposter
    React.useEffect(()=>{
        const script = document.createElement("script");
        script.src = "/path/to/twividFile.js";
        script.async = true;
        document.body.appendChild(script);
    },[]);
    
    return (
    <>
        <div className = "video vid-file-main">
            <div className = "vchat-holder"> 
                <Chatinv/>
            </div>    
            <div className="video-preview" style = {{backgroundImage:orgPoster}}>
                <button className="video-start-btn" >
                    <svg width="50" height="50"  xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512">
                        <path d="M256,0C114.833,0,0,114.844,0,256s114.833,256,256,256s256-114.844,256-256S397.167,0,256,0z M357.771,264.969
                            l-149.333,96c-1.75,1.135-3.771,1.698-5.771,1.698c-1.75,0-3.521-0.438-5.104-1.302C194.125,359.49,192,355.906,192,352V160
                            c0-3.906,2.125-7.49,5.563-9.365c3.375-1.854,7.604-1.74,10.875,0.396l149.333,96c3.042,1.958,4.896,5.344,4.896,8.969
                            S360.813,263.01,357.771,264.969z" elevation = "2"  />
                    </svg>
                </button>
            </div>
            <div className="video-top"></div>

            <div className = "anim play-anim"><img className = "anim-img" src = "/static-img/play.png" alt = "as" width="20"height="20"/></div>

            <div className="video-loading">
                <div className="video-loading-spinner"></div>
            </div>
            
            <div className="video-reset">
                <button className="video-reset-btn">Watch again</button>

            </div>
            
            <div className = "video-incoming">
                <h3 className="grad-text">
                    <span>S</span><span>o</span><span>m</span><span>e</span><span>o</span><span>n</span><span>e</span><span> </span>
                    <span>I</span><span>s</span><span> </span>
                    <span>A</span><span>b</span><span>o</span><span>u</span><span>t</span><span> </span> 
                    <span>T</span><span>o</span><span> </span>
                    <span>J</span><span>o</span><span>i</span><span>n</span><span>.</span><span>.</span><span>.</span>
                </h3>
                <h3 className="grad-text reflex">
                    <span>S</span><span>o</span><span>m</span><span>e</span><span>o</span><span>n</span><span>e</span><span> </span>
                    <span>I</span><span>s</span><span> </span>
                    <span>A</span><span>b</span><span>o</span><span>u</span><span>t</span><span> </span> 
                    <span>T</span><span>o</span><span> </span>
                    <span>J</span><span>o</span><span>i</span><span>n</span><span>.</span><span>.</span><span>.</span>
                </h3>
                <h3 className="grad-text">
                    <span>P</span><span>l</span><span>e</span><span>a</span><span>s</span><span>e</span><span> </span>
                    <span>W</span><span>a</span><span>i</span><span>t</span>
                </h3>
                <h3 className="grad-text reflex">
                    <span>P</span><span>l</span><span>e</span><span>a</span><span>s</span><span>e</span><span> </span>
                    <span>W</span><span>a</span><span>i</span><span>t</span>
                </h3>
                
            </div>

            <div className="video-player-controls">
                <div className="video-slider">
                    <div className = "video-slider-tooltip">0.00</div>
                <div className="video-slider-container">
                    <div className="video-slider-bg">
                        <div className="video-slider-buffered"></div>
                        <div className="video-slider-rail"></div>
                        <div className="video-slider-buffer"></div>
                    </div>
                </div>

                </div>
                <div className="row">
                <div className="wrap">
                    <div className="video-control">
                        <button className="video-control-btn" >
                            <svg className="video-control-play" viewBox="0 0 24 24" width="24" height="24" stroke="currentColor" strokeWidth="1.5" strokeLinecap="round" strokeLinejoin="round" shapeRendering="geometricPrecision">
                            <polygon points="5 3 19 12 5 21 5 3" fill="currentColor"></polygon>
                            </svg>
                            <svg className="video-control-pause" viewBox="0 0 24 24" width="24" height="24" stroke="currentColor" strokeWidth="1.5" strokeLinecap="round" strokeLinejoin="round" shapeRendering="geometricPrecision">
                            <rect x="6" y="4" width="4" height="16" fill="currentColor"></rect>
                            <rect x="14" y="4" width="4" height="16" fill="currentColor"></rect>
                            </svg>

                        </button>
                    </div>
                    <div className="video-counts"><span className="video-count-time">0:00</span><span className="video-count-line">/</span><span className="video-count-fulltime">0:00</span></div>
                    {/* <div className="video-pixel-rate">
                        <div className="video-pixel-box">
                            <div className="video-pixel" >Low</div>
                            <div className="video-pixel" >Medium</div >
                            <div className="video-pixel" >High</div>
                        </div>
                        <div className = "video-pixel-speed">
                            Low
                        </div>
                    </div> */}
                    
                </div>
                <div className="wrap">
                    <div className="video-chat" >
                        <img id = "chat-on" src = "/static-img/chat_white.png" alt = "pip" width="24" height = "24"></img>
                        <img id = "chat-off" src = "/static-img/chat_off.png" alt = "pip" width="24" height = "24"></img>
                    </div>

                    <div className="video-voice">
                        <div className="video-voice-slider">
                            <div className="video-voice-slider-rail" role="slider" aria-valuemax="1" aria-valuemin = "0" aria-valuenow="1">
                                <div className="video-voice-slider-buffer"></div>
                                <input className="video-voice-slider-range" type="range" min="0" max="1" defaultValue="1" step="0.01" />
                            </div>
                        </div>
                        

                        <button className="video-voice-btn">
                            <svg className="video-voice-on" viewBox="0 0 24 24" width="24" height="24" stroke="currentColor" strokeWidth="1.5" strokeLinecap="round" strokeLinejoin="round" fill="none" shapeRendering="geometricPrecision">
                            <path d="M11 5L6 9H2v6h4l5 4V5z"></path>
                            <path d="M19.07 4.93a10 10 0 010 14.14M15.54 8.46a5 5 0 010 7.07"></path>
                            </svg>
                            <svg className="video-voice-off" viewBox="0 0 24 24" width="24" height="24" stroke="currentColor" strokeWidth="1.5" strokeLinecap="round" strokeLinejoin="round" fill="none" shapeRendering="geometricPrecision" >
                            <path d="M11 5L6 9H2v6h4l5 4V5z"></path>
                            <path d="M23 9l-6 6"></path>
                            <path d="M17 9l6 6"></path>
                            </svg>
                        </button>

                    </div>

                    <div className="video-playback-rate">
                        <div className="video-playback-box">
                            <div className="video-playback" >0.25</div><div className="video-playback">0.50</div ><div className="video-playback">0.75</div>
                            <div className="video-playback">Normal</div>
                            <div className="video-playback">1.25</div><div className="video-playback">1.50</div><div className="video-playback">1.75</div>
                            <div className="video-playback">2.0</div>
                        </div>
                        <div className = "video-playback-speed">
                            Normal
                        </div>
                    </div>
                    
                    <div className="video-pip" disabled>
                        <img src = "/static-img/pip_white.png" alt = "pip" width="24" height = "24"></img>
                    </div>
                    
                    <div className="full-screen">
                    <button className="full-screen-btn">
                        <svg className="full-screen-open" viewBox="0 0 24 24" width="24" height="24" stroke="currentColor" strokeWidth="1.5" strokeLinecap="round" strokeLinejoin="round" fill="none" shapeRendering="geometricPrecision">
                        <path d="M15 3h6v6"></path>
                        <path d="M9 21H3v-6"></path>
                        <path d="M21 3l-7 7"></path>
                        <path d="M3 21l7-7"></path>
                        </svg>

                        <svg className="full-screen-exit" viewBox="0 0 24 24" width="24" height="24" stroke="currentColor" strokeWidth="1.5" strokeLinecap="round" strokeLinejoin="round" fill="none" shapeRendering="geometricPrecision">
                        <path d="M4 14h6v6"></path>
                        <path d="M20 10h-6V4"></path>
                        <path d="M14 10l7-7"></path>
                        <path d="M3 21l7-7"></path>
                        </svg>

                    </button>

                    </div>
                </div>
                </div>
            </div>
            
            <div className="video-player">
                <video  id = "vid-file-stream" poster = {poster} tabIndex="-1" datavideo="" preload="auto" controlsList="nodownload" crossOrigin = "anonymous">
                    <source def = {video_url} id = "org-video-src" src={video_url} type="video/mp4"/>
                </video>

            </div>

            
        </div>
        <canvas id="canvas"></canvas>
    </>
)
}
export {Video as default};