import React from 'react'
import './styles/login.css'

const Login = () => {
    React.useEffect(()=>{
        const script = document.createElement("script");
        script.src = "/path/to/login.js";
        script.async = true;
        document.body.appendChild(script);
    },[]);
    return(
        <>
            <div className = 'login-bg'>
                <div className = "wrong-message">
                    The Email or Password is incorrect
                </div>
                <div className = "login">
                    <div className = "caurosel">
                        <div className = "caurosel-container">
                            <div className = "temp">
                                <div className="temp-cover">Create a Party</div>
                                <div className="temp-cover">Invite Your Friends</div>                            
                            </div>
                            <div className = "temp">
                                <div className="temp-cover">Share Your Video File</div>
                                <div className="temp-cover">Watch Along</div>
                                
                            </div>
                            <div className = "temp">
                                <div className="temp-cover">Experience Endless Entertainment</div>
                                
                            </div>
                        </div>
                        <div className = "changer">
                            <div className = "ind-changer" count = "0"/>
                            <div className = "ind-changer" count = "1"/>
                            <div className = "ind-changer" count = "2"/>
                            <div className="ind-cover"/>
                        </div>
                    </div>

                    <div className="login-signup">
                        <div className="button">
                            <button id = "login">Login</button>
                            <button id = "signup">Sign Up</button>
                            {/* <div className = "slider"/> */}
                        </div>
                        <div className="lsmain">
                            <div className="l">
                                <div className = "circle-temp"/>
                                <div className = "circle-temp1"/>    
                                <div className = "circle-temp2"/>    
                                <div className = "underline">
                                    <span className="icon-surround">
                                        <i className = "fas fa-user icon" ></i>
                                    </span>
                                    <input required type = 'email' id = 'lemail' placeholder="Your Email"/>
                                </div>
                                <div className = "underline">
                                    <span className="icon-surround">
                                        <i className = "fas fa-unlock icon" ></i>
                                    </span>
                                    <input required type = 'password' id = "lpass" placeholder="Your Password"/>
                                    <i className="fas fa-eye icon3"></i>
                                    <i className="fas fa-eye-slash icon3" ></i>
                                </div>
                                <div className = "submit lsub">Sign In</div>
                            </div>
                            <div className="s">
                                <div className = "circle-temp"/>
                                <div className = "circle-temp1"/>    
                                <div className = "circle-temp2"/>     
                                <div className = "underline">
                                    <span className="icon-surround">
                                        <i className = "fas fa-user icon" ></i>
                                    </span>
                                    <input type = 'text'  spellCheck="false" required id = 'user' placeholder="Your Username"/>
                                    <i className="fas fa-check-circle icon1"></i>
                                    <i className="fas fa-times-circle icon2"></i><span className="tool-tip">Username must be atleast 5 characters</span>
                                </div>
                                <div className = "underline">
                                    <span className="icon-surround">
                                        <i className="fas fa-envelope-open-text icon"></i>
                                    </span>
                                    <input type = 'email' spellCheck="false" required  id = 'email' placeholder="Your Email"/>
                                    <i className="fas fa-check-circle icon1"></i>
                                    <i className="fas fa-times-circle icon2"></i><span className="tool-tip">Please enter a valid Email Id</span>
                                </div>
                                <div className = "underline">
                                    <span className="icon-surround">
                                        <i className = "fas fa-unlock icon" ></i>
                                    </span>
                                    <input required type = 'password' id = 'pass' placeholder="Your Password"/>
                                    <i className="fas fa-eye icon3"></i>
                                    <i className="fas fa-eye-slash icon3"></i>
                                    <i className="fas fa-check-circle icon1"></i>
                                    <i className="fas fa-times-circle icon2"></i><span className="tool-tip">Password must be atleast 8 Characters</span>
                                </div>
                                <div className = "underline">
                                    <span className="icon-surround">
                                        <i className="fas fa-key icon"></i>
                                    </span>
                                    <input required type = 'password' id = 'cpass' placeholder="Confirm Your Password"/>
                                    <i className="fas fa-eye icon3"></i>
                                    <i className="fas fa-eye-slash icon3"></i>
                                    <i className="fas fa-check-circle icon1"></i>
                                    <i className="fas fa-times-circle icon2"></i><span className="tool-tip">Passwords doesn't match</span>
                                </div>
                                <div className = "submit ssub">Sign Up</div>
                            </div>
                        </div>
                    </div>
                
                </div>
                
            </div>
        </>
    )
}
export {Login as default}