const videoReducer = (state,action) =>{
    switch(action.type){
      case 'SET_VOICE':
            return { ...state,voice:action.voice}
      case 'SET_AUDOP':
            return { ...state,voiceOp:action.voiceOp}
      case 'SET_URL':
          return {...state,url:action.url}
      case 'SET_VIDEO':
        return {...state,video:action.video}
      case 'SET_ROOM':
        return {...state,room:action.room}
      case 'SET_NAME':
        return {...state,name:action.name}
      case 'SET_USER':
        return {...state,user:action.user}
      case 'SET_CUR':
        return {...state,curV:action.curV,curA:action.curA}
      case 'SET_OPTIONS':
        return {...state,options:action.options}
      case 'SET_VIDEO_URL':
        return {...state,videoUrl:action.videoUrl}
      case 'SET_EVERYTHING':
        return {...state,voice:action.voice,voiceOp:action.voiceOp,video:action.video,room:action.room,
                curV:action.curV,curA:action.curA,options:action.options,user:action.user  
        }
      default:
        return state
    }
  }

  export {videoReducer as default}