import React,{useContext,useRef,useEffect, useState} from "react";
import videoContext from '../context/video-context'
import './styles/createRoom.css';
import $ from 'jquery';
import aes from 'crypto-js/aes'
import axios from 'axios'
const videoConstraints = {
    height: '100%',
    width: '100%'
};



const CreateRoom = (props) => {

    const {video,dispatch} = useContext(videoContext)
    const userVideo = useRef();
    const value = props.roomID?"Join Room":"Create Room"

    var audioHtml = '<div  id="audioDevice" class = "drp-dwn-box"><div class = "set-prelim">INPUT DEVICES</div>'
    var videoHtml = '<div  id="videoDevice" class = "drp-dwn-box"><div class = "set-prelim">VIDEO DEVICES</div>'
    var audioopHtml = '<div class = "set-prelim">OUTPUT DEVICES</div>'
    var audioCount=0,videoCount=0,audD = false,vidD = false,curA = false,curV = false;

    const [rooms,setRooms] = useState([]);
    navigator.mediaDevices.enumerateDevices().then(devices =>{
        $('.vid-on,.aud-on').fadeOut();
        
        devices.forEach((device) => {
            if(device.kind === 'audioinput'){
                if(device.label != ''){
                    curA = true;
                    if(audioCount == 0){audD = device.deviceId}
                    audioCount +=  1;
                    $('.aud-off').fadeOut(300);$('.aud-on').fadeIn(300);
                    audioHtml += '<div class="options aud-opt" value="'+device.deviceId+'">'+device.label+'</i></div>'
                }
                else{
                    audioHtml += '<div class="options" >Default</i></div>'
                }
            }
            else if(device.kind === 'audiooutput'){
                if(device.label!='')
                    audioopHtml += '<div class="options aud-op-opt" value="'+device.deviceId+'">'+device.label+'</i></div>'
                else
                    audioopHtml += '<div class="options" >Default</i></div>'
            }
            else if(device.kind === 'videoinput'){
                if(device.label != ''){
                    if(videoCount == 0){vidD = device.deviceId}
                    curV = true;
                    videoCount += 1;
                    $('.vid-off').fadeOut(300);$('.vid-on').fadeIn(300);
                    videoHtml += '<div class="options vid-opt" value="'+device.deviceId+'">'+device.label+'</i></div>'
                }
                else{
                    videoHtml += '<div class="options" >Default</i></div>'
                }
            }
        });
        navigator.mediaDevices.getUserMedia({  
            video: vidD?{deviceId:{exact:vidD}}:false,
            audio: audD?{deviceId:{exact:audD}}:false
        })
        .then(stream => {
            if(userVideo.current)
            { userVideo.current.srcObject = stream;}
        }).catch(err => {console.log(err.message)})
        audioHtml += (audioopHtml+'</div>')
        videoHtml += '</div>'
        
    })
    useEffect(()=>{
        console.log('local storage : ',localStorage.getItem('vid-token'),localStorage.getItem('id'))
        axios.get('https://watch-party-latest.herokuapp.com/room/me',{
            headers:{
                Authorization: 'Bearer ' + localStorage.getItem('vid-token')
            }
        })
        .then(res => {
            setRooms(res.data)
        })
        .catch(err => {
            console.log(err);
        })
    },[])
    
    const audOn = () => {
        navigator.mediaDevices.getUserMedia({ 
            video: curV?{deviceId: {exact: vidD},videoConstraints}:false, 
            audio: {deviceId: {exact: audD}}
        }).then(stream => {
            if(userVideo.current)
            { userVideo.current.srcObject = stream;}
            $('.aud-off').fadeOut(1000);$('.aud-on').fadeIn(1000);
        }).catch(err=>{
            alert('Permission Denied');
        })
        curA = true;
    }
    const vidOn = ()=>{
        navigator.mediaDevices.getUserMedia({  
            video: {deviceId: {exact: vidD},videoConstraints},
            audio:curA?{deviceId:{exact:audD}}:false
            }).then(stream => {
                if(userVideo.current)
                { userVideo.current.srcObject = stream;}
                $('.vid-off').fadeOut(1000);$('.vid-on').fadeIn(1000);
            }).catch(err=>{
                alert('Permission Denied');
            })
        curV = true;
    }
    const printError = (item) =>{
        $('.crwrong-text').html('Room - <span class = "error-room">'+item+'</span> has already been created')
        $(".crwrong-message").css({left:'2%',transform:'scale(1.0)'})
        setTimeout(()=>{
            $(".crwrong-message").css({left:'-50%',transform:'scale(0.5)'})
        },5000)
    }   
    function create(e,k = 0) {
        if(!props.roomID && $('.join-room').val().trim()==''){
            return;
        }
        var id;
        if(!props.roomID){
            var roomName = $('.join-room').val().trim();
            var abc = aes.encrypt(video.user.user._id+' '+roomName,'def');
            id = abc.toString();
            axios.post('https://watch-party-latest.herokuapp.com/room/create',{roomId:id,name:roomName},{headers: {
                Authorization: 'Bearer ' + video.user.token 
              }})
            .then(res=>{
                disp(id)
            })
            .catch(err=>{
                printError(roomName);
                return;
            })
        }    
        else{
            id = props.roomID;
            axios.post('https://watch-party-latest.herokuapp.com/room/add',{id:video.user.user._id,roomId:props.roomID})
            .then(res=>{
                if(res.data.status == 'NA' && k==0){
                    $('.crwrong-text').text('You have already joined')
                    $(".crwrong-message").css({left:'2%',transform:'scale(1.0)'})
                    setTimeout(()=>{
                        $(".wanna-join").css({top:'5%',transform:'scale(1.0)'})
                        $(".crwrong-message").css({left:'-50%',transform:'scale(0.5)'})
                    },4010)
                    return;
                }
                else{
                    disp(id);
                }
            })
            .catch(err=>{
                console.log(err)
            })
        }
        e.preventDefault()
        
    }

    function disp(id){
        dispatch({
            type:'SET_EVERYTHING',
            voice : audD,
            voiceOp:audD,
            curA,curV,
            video : vidD,
            options:{audio:audioHtml,video:videoHtml},
            room:true,
            user:{...video.user,room:id}
        })
        window.user = {...video.user,room:id};
    } 

    const vidOff = ()=>{
        stop('video');
        curV = false;
        userVideo.current.pause();
        $('.vid-on').fadeOut(1000);$('.vid-off').fadeIn(1000);
    }
    const audOff = ()=>{
        stop('audio');
        curA = false;
        $('.aud-on').fadeOut(1000);$('.aud-off').fadeIn(1000);
    }
    const stop = k => userVideo.current.srcObject.getTracks().map(async(t) =>{ if(t.kind == k){await t.stop();}});
    
    const roomPrint = (e)=>{
        if($('.join-room').val() == ''){
            $('.crlunderline').css({top:'40%'});
        }
        else{
            $('.crlunderline').css({top:'21.5%'});
        }
        $('.room-name').text($('.join-room').val());
    }
    function getRandomInt(min, max) {
        min = Math.ceil(min);
        max = Math.floor(max);
        return Math.floor(Math.random() * (max - min) + min);
    }
    function getColor(){
        var r = [getRandomInt(10,250), getRandomInt(50,250), getRandomInt(10,250)];
        var ind = getRandomInt(0,3);
        var temp = r[ind];r[ind] = r[1];r[1] = temp;
        return 'rgb('+r[0]+','+r[1]+','+r[2]+')';
    }
    function title2Case(stri){
        stri = stri.toUpperCase();
        var k = stri.split(' ');var l;
        if(k.length == 1){k = k[0];return k[0]}
        else {l = k[0];k = k[1];return l[0]+k[0]}
    }
    const changeRoom = (roomid) =>{
        var url = new URL(window.location.href)
        if(roomid == props.roomID) return;
        window.location.href = url.origin + '/room/' + roomid
    }
    const myRoomDisplay = () =>{
        if($('.my-rooms-btn').css('backgroundColor') == 'rgb(25, 175, 40)'){
            $('.available-rooms').css({transform:'translateX(-102%)'})
            $('.my-rooms-btn').css({backgroundColor:'rgb(175,20,20)'})
            $('.video-surround').css({left:'10%'});
            return;
        }
        $('.available-rooms').css({transform:'translateX(0%)'})
        $('.my-rooms-btn').css({backgroundColor:'rgb(25,175,40)'})
        $('.video-surround').css({left:'20.5%'});   
    }
    const changeUrl = () =>{
        window.location.href = (new URL(window.location.href)).origin + '/create/'
    }
    return (
        <>
            <div className="wanna-join" onClick = {(e)=>create(e,1)}>
                Join<br/>Anyway
            </div>
            <div className = "my-rooms-btn" onClick = {()=>myRoomDisplay()}>
                <i className = "fas fa-wine-glass-alt crlicon" ></i>
            </div>
            <div className = "available-rooms">
                <div className="cr-mem-prelim">Rooms Created ({rooms.created?rooms.created.length:0})</div>
                {
                    rooms.created&&
                    rooms.created.map((item,index) => {
                        return(
                            <div key = {index} className = "cr-room" onClick = {()=>changeRoom(item.id)}>
                                <div style = {{background:getColor()}} className="crimg-prof1">{title2Case(item.name)}</div>
                                {item.name} 
                            </div>
                        )
                    })
                }
                <div className="cr-mem-prelim">Other Rooms ({rooms.member?rooms.member.length:0})</div>
                {
                    rooms.member&&
                    rooms.member.map((item,index) => {
                        return(
                            <div key = {index} className = "cr-room" onClick = {()=>changeRoom(item.id)}>
                                <div style = {{background:getColor()}} className="crimg-prof1">{title2Case(item.name)}</div>
                                {item.name} 
                            </div>
                        )
                    })
                }
            </div>
            
            <div className = "create-room-heading">{props.roomID?'Join':'Create'} Room</div>
            <div className = "video-surround">
                <video muted ref={userVideo} autoPlay playsInline className = "crvideo"/>
                <div className = "vid vid-off cricon-surround" onClick = {vidOn} ><i className="fas fa-video-slash icon"></i></div>
                <div className = "vid vid-on cricon-surround" onClick = {vidOff}><i className="fas fa-video icon"></i></div>
                <div className = "aud aud-off cricon-surround" onClick = {audOn}><i className="fas fa-microphone-slash icon"></i></div>
                <div className = "aud aud-on cricon-surround" onClick = {audOff}><i className="fas fa-microphone icon"></i></div>
            </div>
            <div className = "crwrong-message">
                <i className="fas fa-times-circle"></i>
                <span className = "crwrong-text"></span>
            </div>
            <div style = {{right:props.roomID?'40px':'70px'}} className = "crl">
                <div className = "circle-temp"/>
                <div className = "circle-temp1"/>
                <div className = "circle-temp2"/>
                {
                    props.roomID?
                    <div className = "room-name">{props.RoomName}</div>
                    :
                    <>
                        <div className = "room-name"></div>
                        <div className = "crlunderline">
                            <span className="crlicon-surround">
                                <i className = "fas fa-wine-glass-alt crlicon" ></i>
                            </span>
                            <input onChange = {(e)=>roomPrint(e)} type = "text" className = "join-room" placeholder = "Room Name"/>
                        </div>
                    </>
                }      
                <button className = "crbtn" onClick = {create}>{value}</button>
            </div>
            {
                props.roomID&&
                    <button className = "crbtn createbtn" onClick = {changeUrl}>Create a Room</button>

            }
            
        </>
    );
};

export default CreateRoom;
