import React, { useEffect,useState,useContext, useRef } from 'react';
import './styles/videoList.css';
import axios from 'axios';
import $ from 'jquery'
import VideoPlayer from './Video'
import videoContext from '../context/video-context'
import ListVideos from './ListVideos'
import VideoFilePlayer from './VideoFile'
import Peer from 'simple-peer'

const VideoList = ({socket})=>{
    const {video} = useContext(videoContext)
    const [videosList,setVideosList] = useState([]);
    const [videosList1,setVideosList1] = useState([]);
    const [vidUrl,setVidUrl] = useState({});
    const userVideo = useRef();
    const streamRef = useRef();
    const createPeersRef = useRef([]);
    const createStreamRef = useRef();
    const addPeersRef = useRef();
    const socketRef = useRef();
    const [users,setUsers] = useState(false);
    const [url_set,setUrlset] = useState(false);

    useEffect(()=>{
        if(socket){
            socketRef.current = socket;
            socketRef.current.emit("join room file", {roomID:video.user.room,type:'help'});
                
            socketRef.current.on("all users file", users => {
                console.log('all users : ',users,$('#vid-file-stream')[0])
                if(!($('#vid-file-stream')[0])) return;
                var stream = $('#vid-file-stream')[0].captureStream();
                createStreamRef.current = stream;
                console.log('other users : ',users);
                users.forEach((id) => {
                    const peer = createFilePeer(id, socketRef.current.id,stream);
                    var temp = {peerID: id,peer}
                    createPeersRef.current.push(temp)
                })
            })
            
            socketRef.current.on("user joined file", payload => {
                console.log('user joined file running...')
                const peer = addFilePeer(payload.signal, payload.callerID);
                addPeersRef.current = peer;
                peer.on('stream',stream => {
                    console.log(stream);
                    userVideo.current = 'copy'
                    streamRef.current = stream
                    setUsers(a => !a)
                })
                setUsers(a=>!a)
            });
            socketRef.current.on("receiving returned signal file", payload => {
                const item = createPeersRef.current.find(p => p.peerID === payload.id);
                item.peer.signal(payload.signal);
            });
            var URL = window.URL || window.webkitURL
            $('#vid-file-select').on('change',function (e) {
                var filetemp = this.files[0]
                if(!filetemp) return;
                var type = filetemp.type
                var videoNode = document.createElement('video')
                var canPlay = videoNode.canPlayType(type)
                if(canPlay == '') return;
                var fileUrl = URL.createObjectURL(filetemp)
                
                userVideo.current = fileUrl;
                setUsers(a => !a)
                
            })
            socketRef.current.on('video end file',()=>{
                streamRef.current = null;
                userVideo.current = '';
                window.fileOwner = '';
                addPeersRef.current.destroy();
                setUsers(a => !a)
            })
        }
    },[socket])

    function createFilePeer(userToSignal, callerID, stream) {
        
        const peer = new Peer({
            initiator: true,
            trickle: false,
            stream,
        });
        peer.on("signal", signal => {
            socketRef.current.emit("sending signal file", { userToSignal, callerID, signal,vid:$('video')[0].duration })
        })
        return peer;
    }

    function addFilePeer(incomingSignal, callerID) {
        const peer = new Peer({
            initiator: false,
            trickle: false,
        })
        window.fileOwner = callerID
        peer.on("signal", signal => {
            socketRef.current.emit("returning signal file", { signal, callerID })
        })

        peer.signal(incomingSignal);

        return peer;
    }

    useEffect(()=>{
        axios.get('https://watch-party-latest.herokuapp.com/video/all')
        .then(res => {
            setVideosList(res.data.video);
            assignVideos(res.data.video);
        })
    },[])
    function assignVideos(arr){
        var k = []
        for(var i = 0;i<4&&i<arr.length;i++){
            k.push(arr[i])
        }
        setVideosList1(k);
    }
    useEffect(()=>{
        if(socket){
            setTimeout(()=>{
                socket.emit('any-video-selected',video.user.room);
                socket.on('video-selected',function(item){
                    setVidUrl(item)
                })
                socket.on('any-video-selected',function (item) {
                    console.log('any-video-selected running and running ',window.vidUrl,item,socket.id)
                    socket.emit('yes-video-selected',{vidUrl:(window.vidUrl?window.vidUrl:{}),to:item})
                })
                socket.on('yes-video-selected',function (item) {
                    console.log('yes-video-selected',item)
                    setVidUrl(item)
                })
            },500);
        }
    },[socket])


    if(socket){
        const viewVideo = (item) => {
            window.vidUrl = item;
            if(vidUrl.startedBy){
                alert('You aren\'t allowed to close this video');
                return;
            }
            socket.emit('video-selected',{item:item,roomID:video.user.room,startedBy:video.user.user._id});
            setVidUrl(item)
        }
        const closeFileVideo = ()=>{
            if(window.fileOwner) return;
            createPeersRef.current.map(item => {
                item.peer.destroy();
            })
            socketRef.current.emit('video end file',video.user.room)
            userVideo.current = ''
              
        }
        var vid;
        const playUrl = (e) => {
            var k = $('.vlrl .crbtn');
            if(k.find('.cr-loading-spinner').length){
                return;
            }
            var reg = /((([A-Za-z]{3,9}:(?:\/\/)?)(?:[-;:&=\+\$,\w]+@)?[A-Za-z0-9.-]+(:[0-9]+)?|(?:www.|[-;:&=\+\$,\w]+@)[A-Za-z0-9.-]+)((?:\/[\+~%\/.\w-_]*)?\??(?:[-\+=&;%@.\w_]*)#?(?:[\w]*))?)/
            var str = $('.join-room').val()
            if(str.match(reg) && str.match(reg).length != 0){
                k.text('')
                k.append('<div className="cr-loading-spinner"/>');
                vid = document.createElement('video')
                vid.src = str;
                $(vid).on('error',function(){
                    $('.vlrl-wr-text').text('Sorry, Unable to play the url')
                    $('.vlrl-wr-message').css({left:'3%',transform:'scale(1.0)'})
                    k.text('Play Url')
                    setTimeout(()=>{$('.vlrl-wr-message').css({left:'-33%',transform:'scale(0.5)'})},4000)
                })
                $(vid).on('canplay',function(){
                    viewVideo({video:{video:vid.src,poster:''}})
                    k.text('Play Url')
                })
            }
            else{
                $('.vlrl-wr-text').text('Please enter a valid url');
                $('.vlrl-wr-message').css({left:'3%',transform:'scale(1.0)'})
                setTimeout(()=>{$('.vlrl-wr-message').css({left:'-33%',transform:'scale(0.5)'})},4000)
                return;
            }
            

        }
        const closeUrlVideo = (e) => {
            if($(e.target).hasClass('url-container') == false) return;
            $('.vlrl').css({transform:'scale(0)',borderRadius:'200px'});
            setTimeout(()=>{
                setUrlset(false)
            },600)
        }
        const openUrlVideo = () => {
            setUrlset(true);
            $('.url-container').fadeIn(10);
            setTimeout(()=>{
                $('.vlrl').css({transform:'scale(1)',borderRadius:'40px'});                
            },50);
        }
        
        return(
            <>
                {vidUrl.video?
                    <>
                        {vidUrl.startedBy?
                            null
                        :
                            <div className = "vv-back" onClick = {()=>viewVideo({})}>
                                <i className="fas fa-times"></i>
                            </div>
                        }
                        <div className = 'vvideo-holder'>
                            <VideoPlayer vid = {vidUrl} />
                        </div>
                    </>
                :   
                    <>
                    {userVideo.current?
                        <>
                            {
                                window.fileOwner?
                                    null
                                :
                                    <div className = "vv-back" onClick = {()=>closeFileVideo()}>
                                        <i className="fas fa-times"></i>
                                    </div>
                            }
                            <div className = 'vvideo-holder'>
                                <VideoFilePlayer vid={userVideo.current} stream = {streamRef.current} />
                            </div>
                        
                        </>
                    :
                        <>
                            {
                                url_set&&
                                <div className = "url-container" onClick = {(e)=>{closeUrlVideo(e)}}>
                                    <div className = "vlrl-wr-message">
                                        <i className="fas fa-times-circle"></i>
                                        <span className = "vlrl-wr-text"></span>
                                    </div>
                                    <div className = "vlrl">
                                        <div className = "circle-temp"/>
                                        <div className = "circle-temp1"/>
                                        <div className = "circle-temp2"/>
                                        <div className = "crlunderline">
                                            <span className="crlicon-surround">
                                                <i className = "fas fa-link crlicon" ></i>
                                            </span>
                                            <input type = "url" className = "join-room" placeholder = "Paste a Url here"/>
                                        </div>
                                        <button className = "crbtn" onClick = {(e)=>{playUrl(e)}}>Play Url</button>
                                    </div>
                                </div>
                            }
                            <div className="vlholder">
                                <input type="file" id = "vid-file-select" className = "vlinnercircle" accept="video/*"/>
                                <i className="fas fa-share-alt vlk-vid-upload"></i>
                                <div className = "vlano-holder">
                                    Share a Video
                                </div>   
                            </div>
                            <div className = "vlholder1" onClick = {()=>openUrlVideo()}>
                                <div className="vlinnercircle1"/>
                                <i className="fas fa-link vlk-vid-upload1"></i>
                                <div className = "vlano-holder1">
                                    Share a Link
                                </div>   
                            </div>
                            
                            <ListVideos videosList = {videosList} videosList1 = {videosList1} viewVideo = {(i)=>viewVideo(i)} />
                        </>
                    }
                    </>
                }         
            </>
        )
    }
    else{
        return null;
    }
}

export {VideoList as default}