import React from 'react'
import { Route ,Redirect} from 'react-router-dom'
import SelectMedia from '../routes/selectMedia'
import aes from 'crypto-js/aes'
import crypto from 'crypto-js'


export const CheckRouter = ({
    alreadySet,
    component : Component,
    ...rest
}) =>{
        var k = (new URL(window.location.href)).pathname;
        var roomID = k.replace('/room/','');
        var def = aes.decrypt(roomID,'def');
        var k = def.toString(crypto.enc.Utf8);

        var RoomName = k.substr(k.indexOf(' ')+1);
        // console.log(k.substr(k.indexOf(' ')+1));
        if(RoomName == ''){
            alert("Sorry No Such Rooms...");
            roomID = '';RoomName = '';
            window.location.href = (new URL(window.location.href)).origin + '/create';
        }
    return (
     <Route {...rest} component={(props) =>(
            !!alreadySet ? (
                <Component /> 
            ) : (
                <SelectMedia roomID={roomID} RoomName = {RoomName} {...props}/>
            )
        )} />
)
}

export default CheckRouter