import React from 'react';
import './styles/chat.css';
import $ from 'jquery';
import dayjs from 'dayjs';
const Chat = ({chat,id})=>{
    function addZero(num){
        if(num/10<1){
          return '0'+num;
        }
        return num;
    }
    React.useEffect(()=>{
        const script = document.createElement("script");
        script.src = "/path/to/chat.js";
        script.async = true;
        document.body.appendChild(script);
    },[]);
    function getRandomInt(min, max) {
        min = Math.ceil(min);
        max = Math.floor(max);
        return Math.floor(Math.random() * (max - min) + min);
    }
    function getColor(){
        var r = [getRandomInt(10,250), getRandomInt(50,250), getRandomInt(10,250)];
        var ind = getRandomInt(0,3);
        var temp = r[ind];r[ind] = r[1];r[1] = temp;
        return 'rgb('+r[0]+','+r[1]+','+r[2]+')';
    }
    const options = {
        root: document.querySelector('ch-o-bt'),
        rootMargin: '0px',
        threshold: [0.1]
    }
    function callback (entries, observer) {
        entries.forEach(entry => {
            if (entry.intersectionRatio > 0.1) {
                $(entry.target).css({transform:' translateX(0%)'})
            } else {
                if($(entry.target).hasClass('message-mine'))
                    $(entry.target).css({transform:' translateX(50%)'})
                else
                    $(entry.target).css({transform:' translateX(-50%)'})
            }
        });
    }
    let observer = new IntersectionObserver(callback, options);
    
    const _MS_PER_DAY = 1000 * 60 * 60 * 24;
    function dateDiffInDays(a, b) {
        const utc1 = Date.UTC(a.getFullYear(), a.getMonth(), a.getDate());
        const utc2 = Date.UTC(b.getFullYear(), b.getMonth(), b.getDate());
        return Math.abs(Math.floor((utc2 - utc1) / _MS_PER_DAY));
    }
    var day_name = ['Sunday','Monday','Tuesday','Wednesday','Thursday','Friday','Saturday'] 
    function dayjs_finder(createdAt){
        var day_js = ''
        var diff = dateDiffInDays(new Date(createdAt),new Date())
        if(diff == 0){
            day_js = 'Today';
        }
        else if(diff == 1){
            day_js = 'Yesterday';
        }
        else if(diff <=6 && diff >= 2){
            day_js = day_name[(new Date(createdAt)).getDay()];
        }
        else{
            day_js = dayjs(createdAt).format('YYYY , D MMM ');
        }
        return day_js
    }
    return (
        <>
            <div className="chat-window">
                <div className = "chat-title">
                    Chat Room
                    <i className="far fa-comment-alt"></i>
                </div>
                <div className="chat-output">

                    <div className = "chat-loader-surround"><div className = "chat-loader"/></div>
                    <div className="ch-o-bt">
                        {chat.length>0?
                            chat.map((t,index)=>{
                                var new_day = false;
                                var day_js = '';
                                if(index!=0){
                                    if(dateDiffInDays(new Date(t.createdAt),new Date(chat[index-1].createdAt)) > 0){
                                        new_day = true;
                                    }
                                }
                                else{
                                    new_day = true;
                                }
                                if(new_day == true){
                                    day_js = dayjs_finder(t.createdAt)
                                }
                                if(index == chat.length-1){
                                    setTimeout(function () {
                                        $('.message-mine').each(
                                            function (ind) {  
                                                observer.observe(this)
                                            }
                                        )
                                        $('.message-other').each(
                                            function (ind) {  
                                                observer.observe(this)
                                            }
                                        )
                                        $('.ch-o-bt')[$('.ch-o-bt').length-1].scrollTop = $('.ch-o-bt')[$('.ch-o-bt').length-1].scrollHeight;
                                                                                
                                    },100);
                                }                               
                                if(t == 'no chat'){
                                    return null;
                                }
                                $('.chat-loader-surround').css({top:'0%'});
                                $('.chat-loader').css({animation:'none'});
                                var temp_color = getColor();
                                var d = new Date(t.createdAt);
                                if(id == t.ownedBy){
                                    return (
                                        <span key = {t._id}>
                                            {
                                                day_js!=''&&
                                                <div className = "chat-date">
                                                    {day_js}
                                                </div>
                                            }
                                            <div  className = "message-mine" >{t.message}<div className="message-time">{addZero(d.getHours())}:{addZero(d.getMinutes())}</div></div>
                                        </span>
                                    )
                                }
                                else{
                                    return(
                                        <span key = {t._id}>
                                            {
                                                day_js!=''&&
                                                <div className = "chat-date">
                                                    {day_js}
                                                </div>
                                            } 
                                            <div key = {t._id} className = "message-other" ><div className = "message-owner" style = {{color:temp_color}}>{t.owner}</div>
                                            {t.message}<div className="message-time">{addZero(d.getHours())}:{addZero(d.getMinutes())}</div></div>
                                        </span>    
                                    )
                                }
                            })
                            :null
                        }
                    </div>
                </div>
                <div className="chat-input">
                    <div className="inp-surround">
                        <textarea  rows ='1' placeholder="Type a Message..."  id = "inp" />
                    </div>
                    <div className = "chat-send">
                        <i class="fas fa-pen-nib"></i>
                    </div>
                </div>
            </div>
        </>
    )
}
export {Chat as default}