import React,{useEffect} from 'react'
import './styles/videoUpload.css'

const VideoUpload = ({addVideo}) =>{
    window.addVideo = addVideo;
    useEffect(()=>{
        const script = document.createElement("script");
        script.src = "/path/to/videoUpload.js";
        script.async = true;
        document.body.appendChild(script);
    },[])
    return(
        <>
            <div className = "circle-temp2"/>
            <div className = "egg-temp">Video<br/>Upload</div>
            <div className = "circle-temp1"/>
            <div className = "circle-temp3"/>
            <div className = "circle-temp4"/>

            <input type = "text" className = "video-name" required placeholder = "Video Name*"/>
            <textarea rows = "10" className = "video-desc" placeholder = "Video Description"/>
            <div className = "file1">
                <input id = "imgfile" type = 'file' className = "innercircle1"/>
                <i className="fas fa-image"></i>
            </div>
            <div className="holder">
                <div className = "ano-holder">
                    <svg className="progress-ring" width="120" height="120">
                        <circle className="progress-ring__circle" stroke="rgb(3,240,250)" strokeWidth="4" fill="transparent" r="53.2" cx="60" cy="60"/>
                    </svg>
                    <input name="file" id = "vidfile" type="file" className="cloudinary-fileupload innercircle" />
                    <i className="fas fa-upload k-vid-upload"></i>
                    <i className="fas fa-check"></i>
                </div>   
            </div>
            
            <div className = "filename_holder">
                <div className = "heading">Poster</div>
                <div className = "poster"></div>
                <div className = "heading">Video</div>
                <div className = "videoflname"></div>
            </div>
            <div className="cloudSubmit">
                <i className="fas fa-arrow-right"></i>
            </div>
            <div className = "cloudCancel">
                <i className="fas fa-times-circle cancelreq"></i>
            </div>
            <div className = "message">
            </div>
            <div className = "wr-message">
            </div>
        </>
    )
}

export {VideoUpload as default}