import React, { useReducer } from 'react';
import { BrowserRouter, Route, Switch } from "react-router-dom";
import CreateRoom from "./routes/CreateRoom";
import Room from "./routes/Room";
import videoContext from './context/video-context'
import videoReducer from './reducers/video'
import CheckRouter from './routers/voiceCheckRouter'
import SelectMedia from './routes/selectMedia'
import Login from './routes/Login'
import axios from 'axios';

function App() {
  const [video,dispatch] = useReducer(videoReducer,[])
  
  React.useEffect(()=>{
    var e_cookie = localStorage.getItem('id');
    var url = window.location.href;  
    url = new URL(url)
    if(!e_cookie){
      if(url.pathname == '/login')return;
      window.redirect = url.pathname == '/'?'/create':url.pathname;
      window.location.href = url.origin + '/login?redirect='+window.redirect;
    }
    else{
      if(!video.user){
        axios.get('https://watch-party-latest.herokuapp.com/users/me',{
          headers: {
            Authorization: 'Bearer ' + localStorage.getItem('vid-token') 
          }
        }).then((res)=>{
          dispatch({
            type:'SET_USER',
            user:{...res.data,token:localStorage.getItem('vid-token')}
          })
        })
      }
      if(url.pathname == '/create')return;
      if(url.pathname != '/login'&& url.pathname != '/'){
        return;
      }
      window.location.href = url.origin + '/create';
    }
  },[])
  return (
    <videoContext.Provider value={{video,dispatch}}>
      <BrowserRouter>
        <Switch>
          <Route path = '/login' component = {Login}/>
          <Route path = "/create" exact component = {CreateRoom} />
          <Route path = "/selectMedia" component = {SelectMedia} />
          <CheckRouter path = "/room/:roomID" component = {Room} alreadySet = {!!video.room} />
        </Switch>
      </BrowserRouter>
    </videoContext.Provider>
  );
}

export default App;