import React, { useContext, useEffect, useRef, useState } from "react";
import io from "socket.io-client";
import Peer from "simple-peer";
import VideoPlayer from '../components/Video';
import './styles/room.css'
import axios from 'axios'
import $ from 'jquery'
import VideoUpload from '../components/VideoUpload'

import videoContext from '../context/video-context'
import Chat from './Chat'
import VideoList from '../components/VideoList'

const videoConstraints = {
    height: 'auto',
    width: 'auto'
};

const UserInfo = (props) =>{
    return(
        <div className = "user-info">
            <div className = "user-info-top">
                <div className = "img-shadow"><div style ={{background:props.col}} className = 'img-prof'>{props.title}</div></div>
                <div className = "user-inform">
                    <div className = "name">{props.user.username}</div>
                    <div className = "email">{props.user.email}</div>
                </div>
            </div>
        </div>
    )
}

const Slider = (props) =>{
    var video_element = props.vid[props.vid.length-1];
    function volCh() {
        var range =  $(this).val();
        $(props.id+' .voice-slider-buffer').css("width", range * 100 + "%");
        video_element.volume = range;
        $(props.id+' .voice-slider-range').attr("value", range);
        if (range == 0) {
            video_element.muted = true;
            $(props.id+' .vol-on').fadeOut(300,function(){$(props.id+' .vol-off').fadeIn(300)});
        } else {
            video_element.muted = false;
            $(props.id+' .vol-off').fadeOut(300,function(){$(props.id+' .vol-on').fadeIn(300);});
        }
    }
    function voCh(e){
        e.stopPropagation();
    }
    useEffect(()=>{
        if(!props.vid) return;
        var to_;
        $(props.id+' .voice-slider').css({width:'0px',visibility:'hidden',opacity:0});
        $(props.id+' .vol-on').fadeIn(300,function(){$(props.id+' .vol-off').fadeOut(300)})
        $(props.id+' .vol-on').on('click',function(e){
            e.stopPropagation();
            $(props.id+' .vol-on').fadeOut(300,function(){$(props.id+' .vol-off').fadeIn(300)});
        })
        $(props.id+' .vol-off').on('click',function(e){
            e.stopPropagation();
            $(props.id+' .vol-off').fadeOut(300,function(){$(props.id+' .vol-on').fadeIn(300);});
        })

        $(props.id+' .vol-on,'+props.id+' .vol-off,'+props.id+' .voice-slider').hover(function(){
            if(to_) clearTimeout(to_)
            $(props.id+' .voice-slider').css({width:'100px',opacity:'1',visibility:'visible'})
            $(props.id+' .volume-changer').css({transform:'translateX(-102px)'});
        },
        function(){
            to_ = setTimeout(function(){
                $(props.id+' .voice-slider').css({width:'0px',opacity:'0',visibility:'hidden'})
                $(props.id+' .volume-changer').css({transform:'translateX(0px)'});
            },2000)
        })
        
        $(props.id+' .voice-slider-range').on("input change", volCh);
        $(props.id+' .voice-slider-range').keyup(volCh).keyup();  

    },[])
    if(!props.vid) return null;
    return(
        <div className = "volume-changer" onClick = {(e)=>voCh(e)}>
            <div className = "vol-surround vol-on"><img src = "/static-img/vol.png" width="17" height = "17"/></div>
            <div className = "vol-surround vol-off"><img src = "/static-img/vol_off.png" width="17" height = "17"/></div>
            
            <div className="voice-slider">
                <div className="voice-slider-rail" role="slider" aria-valuemax="1" aria-valuemin = "0" aria-valuenow="1">
                    <div className="voice-slider-buffer"/>
                    <input className="voice-slider-range" type="range" min="0" max="1" defaultValue="1" step="0.01" />
                </div>
            </div>

        </div>
    )
}
const Video = (props) => {
    $.fn.textWidth = function(text, font) {
        if (!$.fn.textWidth.fakeEl) $.fn.textWidth.fakeEl = $('<span>').hide().appendTo(document.body);
        $.fn.textWidth.fakeEl.text(text || this.val() || this.text()).css('font', font || this.css('font'));
        return $.fn.textWidth.fakeEl.width();
      };
    const [dummy,setDummy] = useState(false)
    const [slider,setSlider] = useState(false);
    const ref = useRef();
    const audref = useRef();

    if(props.user){
        var wid = $('<p>'+props.user.username.toUpperCase()+'</p>').textWidth();
        var nameStyle = {width:wid+15+'px',left:'calc(98% - '+(wid + 10)+'px)'}
    }
    useEffect(() => {
        var temp = false;
        props.peer.on("stream", stream => {
            console.log('on stream running .....',stream.getAudioTracks()[0],stream.getVideoTracks()[0]);
            ref.current.srcObject = stream;
            audref.current.srcObject = stream;

            temp = true;
            props.setSt({id:props.id,stream:stream});
        });
        if(!temp && props.st){
            ref.current.srcObject = props.st.stream
        }
    }, []);
    const hov = (e)=>{
        $('.other .rvideo-pin').html('UN <i className="fas fa-map-pin ricon"></i>')
    }
    const unhov = (e)=>{
        $('.other .rvideo-pin').html('<i className="fas fa-map-pin ricon"></i>')
        
    }
    const metaLoad = ()=>{
        $('#'+props.id+' audio')[0].pause()
        audref.current.srcObject = null;
        setSlider(true);
    }
    const audMetaLoad = ()=>{
        setSlider(true);
    }
    return (
        <React.Fragment>
             <video  onLoadedMetadata = {()=>{metaLoad()}} playsInline autoPlay ref={ref} className="rvideo"/>
             <audio  onLoadedMetadata = {()=>{audMetaLoad()}} playsInline autoPlay ref={audref}/>
            {props.user &&
                <>
                    <div className = "r-container">
                        <div onClick = {(e)=>props.otherUnpin(e)} onMouseEnter={hov} onMouseLeave={unhov} className = "rvideo-pin"><i className="fas fa-map-pin ricon"></i></div>
                        <div style = {nameStyle} className = "rvideo-name">{props.user.username}</div>
                    </div>
                    {slider &&
                        <Slider vid = {$('#'+props.id+' video')} vidRef = {ref} id={'#'+props.id} />
                    }
                </>
            }
        </React.Fragment> 
    );
}

const Room = (props) => {
    const [stre,setStre] = useState([]);
    const [peers, setPeers] = useState([]);
    const socketRef = useRef();
    const userVideo = useRef();
    const peersRef = useRef([]);
    const actualPeersRef = useRef([]);
    const userStream = useRef();
    const senders = useRef([]);
    const [chat,setChat] = useState([]);
    var roomID = window.location.href.substring(window.location.href.lastIndexOf('/') + 1);
    var i=true;
    const {video,dispatch} = useContext(videoContext)
    const [renderer,setRenderer] = useState(false);
    const [members,setMembers] = useState([]);
    const [users,setUsers] = useState([]);
    const [rooms,setRooms] = useState([]);
    const [myvideos,setMyvideos] = useState([]);
    
    function addZero(num){
        if(num == 0) return '0';
        if(num/10<1){
          return '0'+num;
        }
        return num;
    }
    function titleCase(str) {
        return str.toLowerCase().split(' ').map(function(word) {
            return word.replace(word[0], word[0].toUpperCase());
        }).join(' ');
    }

    React.useEffect(()=>{
        const script = document.createElement("script");
        script.src = "/path/to/room.js";
        script.async = true;
        document.body.appendChild(script);
        if(video.curV){$('.vid-off').fadeOut(300);}
        else{$('.vid-on').fadeOut(300);}
        if(video.curA){$('.aud-off').fadeOut(300);}
        else{$('.aud-on').fadeOut(300);}
        
    },[]);
    React.useEffect(()=>{
        axios.post('https://watch-party-latest.herokuapp.com/room/members',{id:video.user.user._id,roomId:video.user.room})
        .then(res=>{
            setMembers(res.data)
        })
    },[])
    React.useEffect(()=>{
        axios.get('https://watch-party-latest.herokuapp.com/video/me',{
            headers:{
            Authorization: 'Bearer '+video.user.token
            }
        })
        .then(res => {
            var vid,videos = res.data.video;
            console.log(videos.length)
            durationfinder(vid,videos,0) 
        })
    },[])

    const addVideo = (vids) =>{
        console.log('vids add video',vids)
        var vid;
        durationfinder(vid,[vids],0);
    }

    function durationfinder(vid,videos,i){
        if(i>=videos.length) {setMyvideos(a=>[...a,...videos]);return;}
        vid = '<video ><source src = "'+videos[i].video+'" type = "video/mp4"></video>'
        $(vid).on('loadedmetadata',function(){
          var hr = addZero(Math.floor(this.duration/3600))
          var min = addZero(Math.floor((this.duration%3600)/60))
          var sec = addZero(Math.floor((this.duration%3600)%60))
          videos[i].duration = (hr=='0'?'':(hr+':'))+min+':'+sec;
          durationfinder(vid,videos,i+1)
        })
      }

    React.useEffect(()=>{
        axios.post('https://watch-party-latest.herokuapp.com/chat/get',{id:video.user.user._id,roomId:video.user.room},{
            headers:{
                Authorization: 'Bearer ' + video.user.token
            }
        })
        .then(res=>{
            setChat(res.data);
        })
        .catch(err=>{
            console.log(err);
        })
    },[]);
    
    
    useEffect(() => {
        socketRef.current = io.connect('https://watch-party-latest.herokuapp.com/',{transports: ['websocket', 'polling', 'flashsocket']});
        window.socket = socketRef.current
        try{
            navigator.mediaDevices.getUserMedia({ 
                video: video.video?{deviceId:{exact:video.video}}:false,
                audio: video.voice?{deviceId:{exact:video.voice}}:false
            })
            .then(stream => {
                if(userVideo.current)
                { userVideo.current.srcObject = stream;}
                if(!video.curA && video.voice){
                    audOff();
                }
                if(!video.curV && video.video){
                    vidOff();
                }
                socketRef.current.emit("join room", {id:video.user.user._id,roomID:video.user.room,type:'normal'});

                socketRef.current.on('duplicate check',function({id}){
                    if(id == video.user.user._id){
                        window.location.href = (new URL(window.location.href)).origin + '/create/'
                    }
                })
                
                socketRef.current.on("all users", users => {
                    const peeers = [];
                    users.forEach((id) => {
                        
                        const peer = createPeer(id, socketRef.current.id, stream);
                        var temp = {peerID: id,peer,user:null}
                        peersRef.current.push(temp)
                        if(peersRef.current.length<15){
                            actualPeersRef.current.push(temp)
                        }
                        peeers.push(peer);
                    })
                    setPeers(users => [...users,...peeers]);
                })

                socketRef.current.on("user joined", payload => {
                    const peer = addPeer(payload.signal, payload.callerID, stream);
                    var temp = {peerID: payload.callerID,peer,user:payload.user}
                    peersRef.current.push(temp)
                    if(peersRef.current.length<15){
                        actualPeersRef.current.push(temp)
                    }
                    setUsers(user => [...user,payload.user])
                });

                socketRef.current.on("receiving returned signal", payload => {
                    const item = peersRef.current.find(p => p.peerID === payload.id);
                    item.user = payload.user
                    item.peer.signal(payload.signal);
                    setUsers(user => [...user,payload.user])
                    // setRenderer(a => !a)
                    // console.log('item user : ',item.user)
                });
            })
            .catch(err=>{
                
                socketRef.current.emit("join room", {id:video.user.user._id,roomID:video.user.room,type:'help'});
                
                socketRef.current.on('duplicate check',function({id}){
                    if(id == video.user.user._id){
                        window.location.href = (new URL(window.location.href)).origin + '/create/'
                    }
                })

                socketRef.current.on("all users", users => {
                    const peeers = [];
                    users.forEach((id) => {
                        const peer = createPeer(id,socketRef.current.id,null);
                        var temp = {peerID: id,peer,user:null}
                        peersRef.current.push(temp)
                        if(peersRef.current.length<15){
                            actualPeersRef.current.push(temp)
                        }
                        peeers.push(peer);
                    })
                    setPeers(users => [...users,...peeers]);
                })

                socketRef.current.on("user joined", payload => {
                    const peer = addPeer(payload.signal, payload.callerID, null);
                    var temp = {peerID: payload.callerID,peer,user:payload.user}
                    peersRef.current.push(temp)
                    if(peersRef.current.length<15){
                        actualPeersRef.current.push(temp)
                    }
                    setUsers(user => [...user,payload.user])
                });

                socketRef.current.on("receiving returned signal", payload => {
                    console.log(peers);
                    const item = peersRef.current.find(p => p.peerID === payload.id);
                    item.user = payload.user;
                    item.peer.signal(payload.signal);
                    setUsers(user => [...user,payload.user])
                });
            })

            socketRef.current.on("user left",id =>{
                const peerObj = peersRef.current.find(p => p.peerID === id)
                if(!peerObj) return;

                var user = peerObj.user;
                const peerObj1 = actualPeersRef.current.find(p => p.peerID === id)
                if(peerObj){
                    peerObj.peer.destroy()
                }
                if(peerObj1){peerObj1.peer.destroy()}
                peersRef.current = peersRef.current.filter(p => p.peerID !== id)
                actualPeersRef.current = actualPeersRef.current.filter(p => p.peerID !== id)
                var k = stre.filter(t => t.peerID!=id);
                setStre([...k]);
                if(actualPeersRef.current.length == 0 && !$('.me').hasClass('rvideo-surround')){
                    otherUnpin(0);
                }
                k = users.filter(t => t._id!=user._id);
                setUsers([...k])
            })
        }
        catch(err){
        }

    }, []);
    function createPeer(userToSignal, callerID, stream) {
        const peer = new Peer({
            initiator: true,
            trickle: false,
            stream,
        });
        peer.on("signal", signal => {
            console.log('sending signal...',callerID);
            socketRef.current.emit("sending signal", { userToSignal, callerID, signal,user:video.user.user })
        })
        return peer;
    }

    function addPeer(incomingSignal, callerID, stream) {
        const peer = new Peer({
            initiator: false,
            trickle: false,
            stream,
        })
        
        peer.on("signal", signal => {
            console.log('returning signal ',callerID)
            socketRef.current.emit("returning signal", { signal, callerID,user:video.user.user })
        })

        peer.signal(incomingSignal);

        return peer;
    }
    React.useEffect(()=>{
        var k = $(video.options.audio)
        $('.container').append(k);
        k = $(video.options.video)
        $('.container').append(k);
        if(!video.voice){ 
            $('#audioDevice .options').css({borderRight:'3px solid rgb(3,240,100)'})
        }    
        else{
            $('.aud-opt').each(function(ind){
                if($(this).attr('value') == video.voice){
                    $(this).css({borderRight:'3px solid rgb(3,240,100)'})
                }
            })
            $('.aud-op-opt').each(function(ind){
                if($(this).attr('value') == video.voiceOp){
                    $(this).css({borderRight:'3px solid rgb(3,240,100)'})
                }
            })
        }
        if(!video.video){ 
            $('#videoDevice .options').css({borderRight:'3px solid rgb(3,240,100)'})
        }    
        else{
            $('.vid-opt').each(function(ind){
                if($(this).attr('value') == video.video){
                    $(this).css({borderRight:'3px solid rgb(3,240,100)'})
                }
            })
        }
        $('.aud-opt').on('click',function(){
            if(video.voice == $(this).attr('value')) return;
            $('.aud-opt').css({borderRight:'none'})
            $(this).css({borderRight:'3px solid rgb(3,240,100)'})
            video.voice = $(this).attr('value')
            audOn();
        })
        $('.vid-opt').on('click',function(){
            if(video.video == $(this).attr('value')) return;
            $('.vid-opt').css({borderRight:'none'})
            $(this).css({borderRight:'3px solid rgb(3,240,100)'})
            video.video = $(this).attr('value');
            vidOn();
        })
        var mouse_is_inside=false;
        $('.drp-dwn-box').hover(function(){ 
            console.log('hover')
            mouse_is_inside=true; 
        }, function(){ 
            console.log('hover not')
            mouse_is_inside=false; 
        });
    
        $(document).mouseup(function(){ 
            if(! mouse_is_inside) $('.drp-dwn-box').fadeOut(100);
        });
    },[])
    
    useEffect(()=>{
        axios.get('https://watch-party-latest.herokuapp.com/room/me',{
            headers:{
                Authorization: 'Bearer ' + video.user.token
            }
        })
        .then(res => {
            setRooms(res.data)
        })
        .catch(err => {
            console.log(err);
        })
    },[])

    
    var curV = false,curA = false;
    if(video.video) curV = true;if(video.voice) curA = true;

    const audOn = () => {
        navigator.mediaDevices.getUserMedia({ 
            video: curV?{deviceId: {exact: video.video},videoConstraints}:false, 
            audio: {deviceId: {exact: video.voice}}
        }).then(stream => {
            var oldt = userVideo.current.srcObject.getAudioTracks()[0]
            if(userVideo.current)
            { 
                userVideo.current.srcObject.removeTrack(oldt)
                userVideo.current.srcObject.addTrack(stream.getAudioTracks()[0])
            }
            var newt = userVideo.current.srcObject.getAudioTracks()[0];
            $('.aud-off').fadeOut(200,function(){$('.aud-on').fadeIn(200);})
            peers.map(t=>{
                t.replaceTrack(oldt,newt,userVideo.current.srcObject)
            })
        }).catch(err=>{
            console.log(err); 
            alert('Permission Denied');
        })
        curA = true;
    }
    const vidOn = ()=>{
        navigator.mediaDevices.getUserMedia({  
            video: {deviceId: {exact: video.video},videoConstraints},
            audio:curA?{deviceId:{exact:video.voice}}:false
        }).then(stream => {
            var oldt = userVideo.current.srcObject.getVideoTracks()[0]
            if(userVideo.current)
            { 
                console.log("current changed .....")
                userVideo.current.srcObject.removeTrack(oldt)
                userVideo.current.srcObject.addTrack(stream.getVideoTracks()[0])
                userVideo.current.play();
            }
            var newt = userVideo.current.srcObject.getVideoTracks()[0];
            $('.vid-off').fadeOut(200,function(){$('.vid-on').fadeIn(200);})
            peers.map(t=>{
                t.replaceTrack(oldt,newt,userVideo.current.srcObject)
            })
        })
        .catch(err=>{
            alert('Permission Denied');
        })
        curV = true;
        

    }
    const vidOff = ()=>{
        var k = userVideo.current.srcObject.getVideoTracks()[0];
        k.enabled = false;
        stop('video');
        curV = false;
        userVideo.current.pause();
        $('.vid-on').fadeOut(200,function(){$('.vid-off').fadeIn(200);})

    }
    const audOff = ()=>{
        var k = userVideo.current.srcObject.getAudioTracks()[0];
        k.enabled = false;
        stop('audio');
        curA = false;
        $('.aud-on').fadeOut(200,function(){$('.aud-off').fadeIn(200);})
        
    }
    const stop = k => userVideo.current.srcObject.getTracks().map(async(t) =>{ if(t.kind == k){await t.stop();}});
    var wid = 1100,ht = 620;
    
    const ch = (k = 0)=>{
        var len = Math.min(actualPeersRef.current.length + 1,$('.rvideo-surround').length);
        if(len>=1){
            $('.rvideo-surround').css({width:'1000px',height:'600px'});
        }
        if(len>=2){
            $('.rvideo-surround').css({width:wid/2,height:ht/2});
        }
        if(len>=5){
            $('.rvideo-grid').css({gridTemplateColumns:'auto auto auto'})
            $('.rvideo-surround').css({width:wid/3,height:ht/2.8});
        }
        if(len>=10){
            $('.rvideo-grid').css({gridTemplateColumns:'auto auto auto auto'})
            $('.rvideo-surround').css({width:wid/3.8,height:ht/3.1});
        }
        if(len>=13){
            $('.rvideo-grid').css({gridTemplateColumns:'auto auto auto auto'})
            $('.rvideo-surround').css({width:wid/3.8,height:ht/3.8});
        }
    }
    ch()
    if(!(actualPeersRef.current.length == 0 && peersRef.current.length>0)) 
        $('.me .rvideo-pin').fadeOut();
    const myPin = ()=>{
        $('.me').find('.rvideo-pin').fadeIn(500);
        if(actualPeersRef.current.length == 0){
            return;
        }
        else{
            actualPeersRef.current = []
            $('.other').removeClass('rvideo-surround')
            setRenderer(dummy => !dummy)
            ch();
        }
    }
    const otherPin = (peerID)=>{
        var item = peersRef.current.find(p => p.peerID == peerID);
        // console.log(item);
        $('.me').fadeOut();
        $('.me').removeClass('rvideo-surround');
        $('.other .rvideo-pin').fadeIn(500);
        actualPeersRef.current = [item];
        setRenderer(dummy => !dummy)
        ch();
    }
    const otherPinSel = (user) => {
        // console.log(user);
        const peer = peersRef.current.find(t => t.user._id == user._id);
        otherPin(peer.peerID)
    }
    const otherUnpin = (e)=>{  
        if(e!=0)
            e.stopPropagation(); 
        console.log('running other unpin...')
        var peer = actualPeersRef.current[0];var lim = 15;
        for(var i = actualPeersRef.current.length;i<=lim && i<peersRef.current.length;i++){
            if(peer && peer.peerID == peersRef.current[i].peerID){lim = 16;continue;}
            actualPeersRef.current.push(peersRef.current[i])
        }
        $('.me').addClass('rvideo-surround')
        $('.me').fadeIn();
        ch();
        setRenderer(dummy => !dummy)
    }
    
    const meUnpin =(e)=>{
        $('.me .rvideo-pin').fadeOut();
        if(actualPeersRef.current.length == 0 && peersRef.current.length == 0){
            return;
        }
        if(actualPeersRef.current.length >= 2){
            return;
        }
        e.stopPropagation();
        for(var i = 0;i<15&&i<peersRef.current.length&&actualPeersRef.current.length<peersRef.current.length;i++){
            actualPeersRef.current.push(peersRef.current[i])
        }
        $('.me .rvideo-pin').fadeOut(500);
        setRenderer(dummy => !dummy)
        ch();

    }
    const options = {
        root: document.querySelector('r-members'),
        rootMargin: '0px',
        threshold: [0.1]
    }
    const options1 = {
        root: document.querySelector('r-videos'),
        rootMargin: '0px',
        threshold: [0.1]
    }
    const options2 = {
        root: document.querySelector('r-rooms'),
        rootMargin: '0px',
        threshold: [0.1]
    }  
    function callback (entries, observer) {
        entries.forEach(entry => {
            if (entry.intersectionRatio > 0.1) {
                $(entry.target).css({transform:' translateX(0%)'})
            } else {
                $(entry.target).css({transform:' translateX(-50%)'})
            }
        });
    }
      
    let observer = new IntersectionObserver(callback, options);
    let observer1 = new IntersectionObserver(callback, options1); 
    let observer2 = new IntersectionObserver(callback, options2);  


    function getRandomInt(min, max) {
        min = Math.ceil(min);
        max = Math.floor(max);
        return Math.floor(Math.random() * (max - min) + min);
    }
    function getColor(){
        var r = [getRandomInt(10,250), getRandomInt(50,250), getRandomInt(10,250)];
        var ind = getRandomInt(0,3);
        var temp = r[ind];r[ind] = r[1];r[1] = temp;
        return 'rgb('+r[0]+','+r[1]+','+r[2]+')';
    }
    function title2Case(stri){
        stri = stri.toUpperCase();
        var k = stri.split(' ');var l;
        if(k.length == 1){k = k[0];return k[0]}
        else {l = k[0];k = k[1];return l[0]+k[0]}
    }
    var col = getColor()
    var notJoined = members.filter(i=>!users.map(( user ) => user._id).includes(i._id));
    const changeAudio = ()=>{
        $('#videoDevice').fadeOut(10);
        $('#audioDevice').fadeIn(10);
    }
    const changeVideo = ()=>{
        $('#audioDevice').fadeOut(10);
        $('#videoDevice').fadeIn(10);
    }
    const retStre = (peerID)=>{
       return stre.find(t=>t.id == peerID);
    }
    const changeRoom = (roomid) =>{
        var url = new URL(window.location.href)
        if(roomid == video.user.room) return;
        window.location.href = url.origin + '/room/' + roomid
    }
    const SignOut = () => {
        localStorage.removeItem('id');
        localStorage.removeItem('vid-token');
        var url = new URL(window.location.href)
        axios.post('https://watch-party-latest.herokuapp.com/users/logout',{
            id:video.user.user._id,token:video.user.token
        })
        .then(res => {
            window.location.href = url.origin
            dispatch({
                type:'SET_VOICE',
                voice : false
            })
            dispatch({
                type:'SET_AUDOP',
                voiceOp:false
            })
            dispatch({
                type:'SET_CUR',
                curA:false,curV:false
            })
            dispatch({
                type:'SET_VIDEO',
                video : false
            })
            dispatch({
                type:'SET_OPTIONS',
                options:{audio:'',video:''}
            })
            dispatch({
                type:'SET_ROOM',
                room:false
            })
            dispatch({
                type:'SET_USER',
                user:null
            })
        })
        .catch(err=>{
            console.log('error in logging out');
        })
    }
    return (
        <>

            <div className = "r-dashboard">
                <div className = "dashboard-selector">
                    <div className = "dashboard-option" id = "home">
                        <i className="fas fa-h-square d-opt-icon"></i>
                    </div>
                    <div className = "dashboard-option" id = "vidup">
                        <i className="fas fa-upload d-opt-icon"></i>
                    </div>
                    <div className = "dashboard-option" id = "vidch">
                        <i className="fas fa-video d-opt-icon"></i>
                    </div>
                    <div className = "dashboard-option" id = "vid">
                        <i className="fab fa-youtube d-opt-icon"></i>
                    </div>
                    <div className = "d-opt-selector"/>
                </div>
                
                <div className = "rholder" onClick = {()=>SignOut()}>
                    <div className="rinnercircle"/>
                    <i class="fas fa-sign-out-alt rk-vid-upload"></i>
                </div>

                <div className = "r-rooms">
                    
                    <div className="r-mem-prelim">Rooms Created ({rooms.created?rooms.created.length:0})</div>
                    {
                        rooms.created&&
                        rooms.created.map((item,index) => {
                            return(
                                <div key = {index} className = "r-room" onClick = {()=>changeRoom(item.id)}>
                                    <div style = {{background:getColor()}} className="img-prof1">{title2Case(item.name)}</div>
                                    {item.name} {item.id == video.user.room?<div className = "current-room"/>:null}
                                </div>
                            )
                        })
                    }
                    <div className="r-mem-prelim">Other Rooms ({rooms.member?rooms.member.length:0})</div>
                    {
                        rooms.member&&
                        rooms.member.map((item,index) => {
                            if(index == rooms.member.length-1){
                                setTimeout(function () {
                                    $('.r-room').each(
                                        function (ind) {  
                                            observer2.observe(this)
                                        }
                                    )
                                },100);
                            }
                            return(
                                <div key = {index} className = "r-room" onClick = {()=>changeRoom(item.id)}>
                                    <div style = {{background:getColor()}} className="img-prof1">{title2Case(item.name)}</div>
                                    {item.name} {item.id == video.user.room?<div className = "current-room"/>:null}
                                </div>
                            )
                        })
                    }
                </div>
                <div className = "r-videos">
                    {
                        myvideos.length!=0?
                        <>
                            <div className = "r-mem-prelim">Your Videos</div>
                            {
                                myvideos.map((item,index) =>{
                                    if(index == myvideos.length-1){
                                        setTimeout(function () {
                                            $('.r-video-').each(
                                                function (ind) {  
                                                    observer2.observe(this)
                                                }
                                            )
                                        },100);
                                    }
                                    return(
                                        <div key = {index} className = "r-video-">
                                            <div className = "vid-poster"><img src = {item.poster} width = "100" height = "75"/></div>
                                            <div className = "vid-name">{item.name}</div>
                                            <div className = "vid-views">12 views</div>
                                            <div className = "vid-duration">{item.duration}</div>
                                        </div>    
                                    )
                                })
                            }
                        </>
                        :<div className = "r-mem-prelim">No Videos Uploaded</div>
                    }
                </div>    
                <div className = 'r-members' >
                    <div className = 'r-mem-prelim'>Total Members ({members.length+1})</div>
                    <div className = 'r-mem-prelim'>Online ({users.length+1})</div>
                    <div className = 'r-member' onClick = {(e)=>{myPin(e)}}>
                        <div style = {{background:col}} className="img-prof">{title2Case(video.user.user.username)}</div>
                        {video.user.user.username} (you)
                    </div>
                    
                    {
                        users.map((user,index)=>{
                            if(index == peersRef.current.length-1){
                                setTimeout(function (params) {
                                    $('.r-member').each(
                                        function (ind) {  
                                            observer.observe(this)
                                        }
                                    )
                                },100);
                            }
                            return (
                                <div className = 'r-member' key = {index} onClick = {()=>{otherPinSel(user)}}>
                                    
                                    <div style = {{background:getColor()}} className="img-prof">{title2Case(user.username)}</div>
                                    {user.username}
                                </div>
                            )
                        })
                    }
                    <div className = 'r-mem-prelim'>Offline</div>
                    {
                        notJoined.map((t,index) => {
                            if(index == notJoined.length-1){
                                setTimeout(function (params) {
                                    $('.r-member').each(
                                        function (ind) {  
                                            observer.observe(this)
                                        }
                                    )
                                },100);
                            }
                            return (
                                <div className = 'r-member' key = {index}>
                                    <div style = {{background:getColor()}} className="img-prof">{title2Case(t.username)}</div>
                                    {t.username}
                                </div>
                            )
                        })
                    }

                </div>
            </div>
            
            <div className = "overall-container">
                <div className = "inside-overall-container">
                    <div className = "video-list">
                        <VideoList socket = {socketRef.current} />
                    </div>
                    <div className = "video-upload-container">
                        <VideoUpload addVideo = {(i)=>addVideo(i)}/>
                    </div>
                    <div className = "container">
                        <div className = "circle-temp2"/>
                        <div className = "egg-temp"/>
                        <div className = "circle-temp1"/>
                        <div className = "circle-temp3"/>
                        <div className = "circle-temp4"/>
                    <div className = "rvideo-grid">
                        <div className = "rvideo-surround me" onClick={()=>myPin()}>
                            <video muted width="100%" height='100%' ref={userVideo} autoPlay playsInline className = "rvideo"/>
                            <div className = "r-container">
                                <div onClick = {(e)=>meUnpin(e)} className = "rvideo-pin"><i className="fas fa-map-pin ricon"></i></div>
                                <div className = "rvideo-name">you</div>
                            </div>
                        </div>             
                        {actualPeersRef.current.length > 0?
                            actualPeersRef.current.map(({peerID,peer,user}, index) => {
                                if(index == actualPeersRef.current.length - 1){
                                    setTimeout(function(){ch();
                                        if($('.me').hasClass('rvideo-surround')){
                                            $('.other .rvideo-pin').fadeOut();
                                        }
                                    },300);
                                }
                                return (
                                    <div key={index} id = {peerID} onClick = {()=>otherPin(peerID)} className = "rvideo-surround other">
                                        <Video setSt = {(k)=>{setStre(us=>[...us,k])}} st = {retStre(peerID)} otherUnpin = {(e)=>{otherUnpin(e)}} id = {peerID} peer={peer} user = {user} className = "rvideo"/>  
                                    </div> 
                                );
                            }
                        )
                        :
                        null}
                        
                        
                    </div>
                    

                    <div className = "vid-btn-holder">
                        <div className = "vid vid-off crricon-surround" onClick = {vidOn} ><i className="fas fa-video-slash icon"></i></div>
                        <div className = "vid vid-on crricon-surround" onClick = {vidOff}><i className="fas fa-video icon"></i></div>
                        <div className = "drp-dwn" onClick = {()=>changeVideo()}><i className="fas fa-caret-down"></i></div>
                    </div>
                    <div className = "aud-btn-holder">
                        <div className = "aud aud-off crricon-surround" onClick = {audOn}><i className="fas fa-microphone-slash icon"></i></div>
                        <div className = "aud aud-on crricon-surround" onClick = {audOff}><i className="fas fa-microphone icon"></i></div>
                        <div className = "drp-dwn" onClick = {()=>changeAudio()}><i className="fas fa-caret-down"></i></div>
                    </div>
                </div>
                </div>    
            </div>
            
            <div className = 'ricon-surround ch-open'>
                <i className="fas fa-comment-alt ricon"></i>
                Chat
            </div>
            <div className = 'ricon1-surround ch-close'>
                <i className="fas fa-chevron-right ricon1"></i>
            </div>
            <div className = 'rchat-holder'>
                <Chat id = {video.user.user._id} chat = {chat} />
            </div>        
        </>
    );
};

export default Room;



