import React, { useEffect } from 'react'
import dayjs from 'dayjs'
import relativeTime from 'dayjs/plugin/relativeTime'
import calendar from 'dayjs/plugin/calendar'
import $ from 'jquery'

const ListVideos = ({videosList,videosList1,viewVideo}) =>{
    dayjs.extend(relativeTime)
    dayjs.extend(calendar)
    useEffect(()=>{
        setTimeout(()=>{
            console.log('set Time out running');
            const script = document.createElement("script");
            script.src = "/path/to/videoList.js";
            script.async = true;
            document.body.appendChild(script);   
        },300);
    },[])
    function title2Case(stri){
        stri = stri.toUpperCase();
        var k = stri.split(' ');var l;
        if(k.length == 1){k = k[0];return k[0]}
        else {l = k[0];k = k[1];return l[0]+k[0]}
    }
    function toTitleCase(str) {
        return str.replace(
          /\w\S*/g,
          function(txt) {
            return txt.charAt(0).toUpperCase() + txt.substr(1).toLowerCase();
          }
        );
    }

    return(
        <div className = "video-container">
                <div className = "video-caurosel">
                    <div className = "circle-temp"/>
                    <div className = "circle-temp1"/>
                    <div className = "circle-temp3"/>
                    <div className = "circle-temp4"/>

                    {videosList1.length!=0?
                        <div style = {{width:'100%',height:'100%',postion:'absolute',top:'0',left:'0'}}>
                            <div className = "vid-caurosel-mover" style = {{width:videosList1.length*100+'%',display:'flex',flexDirection:'row',alignItems:'center',height:'100%'}}>
                                {   videosList1.length != 0&&
                                    videosList1.map((item,index)=>{
                                        if(index == videosList1.length - 1){
                                            setTimeout(function(){
                                                var wid = $('.video-caurosel').width();
                                                $('.indv-caurosel').css({width:wid*0.8+'px',marginLeft:wid*0.1,marginRight:0.1*wid})
                                                $('.caurosel-name').css({opacity:1.0});
                                            },500);
                                        }
                                        var url  = 'url("'+item.video.poster+'")'
                                        return(
                                            <div onClick = {()=>viewVideo(item)} style = {{backgroundImage:url}} key={index} className = "indv-caurosel">
                                                <div className = "caurosel-name">{item.video.name}</div>
                                            </div>
                                        )
                                    })
                                }
                            </div>
                        </div>
                    :null}
                    <div className = "prev-caurosel"><i className="fas fa-chevron-left"></i></div>
                    <div className = "next-caurosel"><i className="fas fa-chevron-right"></i></div>
                </div>
                {   videosList.length != 0&&
                    videosList.map((item,index)=>{
                        return(
                            <div key = {index} className = "single-video" onClick = {()=>viewVideo(item)}>
                                <div className = "circle-temp"/>
                                <div className = "circle-temp1"/>
                                <div className = "circle-temp2"/>

                                <div className = "sin-vid-poster"><img src = {item.video.poster} width = "373" height = "250"/></div>
                                <div className = "vvideo-name">{item.video.name}</div>
                                <div className = "vvideo-info-holder">
                                    <div className = "vvideo-views">12 views</div>
                                    <div className = "vvideo-owner">
                                        <div className = "img-prof2">{title2Case(item.user.username)}</div>{toTitleCase(item.user.username)}
                                    </div>
                                    <div className = "vvideo-created">{dayjs(item.video.createdAt).fromNow()}</div>
                                </div>
                            </div>
                        )
                    })
                }
            </div>    
    )
}

export {ListVideos as default}